.hello-there-tt::before {
    height: 100%;
    width: 100%;
    background: url("https://i.imgur.com/UmMvwjn.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 15px;
    text-align: center;
}

.below-hero {
    background: url("https://i.imgur.com/1kP2hFs.jpg");
    display: grid;
    width: 100%;
    place-items: center;
    background-size: cover;
    background-position: center;
}

.below-hero>*{
    grid-column: 1;
    grid-row: 1;
}